import React, { useState } from "react";
import "./Login.css";
import { Link, Redirect, useHistory } from "react-router-dom";
import { auth } from "./firebase";

function Login() {
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const login = (e) => {
    e.preventDefault(); //this stops the refresh

    auth
      .signInWithEmailAndPassword(email, password)
      .then((auth) => {
        //logged in redirect to home page
        history.push("/");
        // return <Redirect to="/" />;
      })
      .catch((e) => alert(e.message));
  };

  const register = (e) => {
    e.preventDefault(); //this stops the refresh
    auth
      .createUserWithEmailAndPassword(email, password)
      .then((auth) => {
        //create a user and logged in, redirect to home page
        history.push("/");
      })
      .catch((e) => alert(e.message));
  };

  return (
    <div className="login">
      <Link to="/">
        <img
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a9/Amazon_logo.svg/1025px-Amazon_logo.svg  .png"
          alt=" logo"
          className="login__logo"
        />
      </Link>
      <div className="login__container">
        <h1>Sign in</h1>
        <form>
          <h5>E-mail</h5>
          <input
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            type="email"
          />
          <h5>Password</h5>
          <input
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            type="password"
          />
          <button onClick={login} type="submit" className="login__signInButton">
            Sign In
          </button>
        </form>
        <p>
          By signin you agree Lorem ipsum dolor sit amet consectetur,
          adipisicing elit. In provident voluptas temporibus et, illo veritatis
          tempora architecto quae obcaecati eaque.
        </p>

        <button onClick={register} className="login__registerButton">
          Create your Account
        </button>
      </div>
    </div>
  );
}

export default Login;
